.main-nav{
    min-height: 10px;
    width: 100%;
    margin: 0% auto;
    /* border-top: solid 3px rgb(225, 106, 20); */
    border-top: solid 3px orange;
    box-shadow: 0px 1px 5px black;
    position: fixed;
    z-index: 5;
    top: 0%;
    left: 0%;
    background-color: #f8f9f9;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar{
    height: 100%;
    min-width: 85%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav-logo{
    padding: 5px 25px;
}

.nav-item{
    margin: 0px 3px;
    font-size: small;
    font-weight: 500;
    text-decoration: none;
    color: rgb(69, 69, 69);
    transition: 0.2s;
}

.nav-btn{
    cursor: pointer;
    border-radius: 20px;
    padding: 10px 20px;
}

.nav-item:hover {
    background-color: rgb(255, 255, 255);
}

.navbar form{
    flex-grow: 1;
    padding: 0px 12px;
    position: relative;
}
.navbar form input{
    min-width: 90%;
    margin: 0;
    padding: 8px 10px 8px 32px;
    font-size: 13px;
    border: solid 1px black;
    border-radius: 3px;
}

.navbar form .search-icon{
    position: absolute;
    left: 25px;
    top: 8px;
}

.nav-links{
    padding: 7Px 13px;
    border: solid 1px blue;
    border-radius: 3px;
    background-color: #e7f8fe;
    cursor: pointer;
}

.nav-links:hover{
    background-color: rgba(229, 148, 72, 0.295);
}

.pro{
    background-color: rgb(46, 225, 219);
    border-radius: 100px;
    border: solid 1px black;
    font-size: 20px;
    padding: 2px;
    margin: 2px;
    padding-left: 6px;
    padding-right: 6px;
}

